@import url(https://fonts.googleapis.com/css?family=Mukta:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ticket {
    margin: 200;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 33vw;
    position: relative;
    background-color: rgb(245, 245, 245);
    border-radius: 10px;
  }
  
  .container{
    display: flex;
    justify-content: space-between;
  }
  
  .email {
    font-weight: bold;
    color: rgb(73, 73, 73);
  }
  .title {
    display: flex;
    justify-content: left;
    font-weight: 1000;
    width: 33vw;
    border-radius: 25px;
  }
  
  #searchInput {
    display: flex;
    justify-content: center;
    border-radius: 25px;
    border-width: 0.2px;
    width: 33vw;
    background-color: rgb(230, 230, 230);
    color: black;
  }
  
  ::-webkit-input-placeholder {
    color: black;
  }
  
  :-ms-input-placeholder {
    color: black;
  }
  
  ::-ms-input-placeholder {
    color: black;
  }
  
  ::placeholder {
    color: black;
  }
  
  .hideTicketButton {
    position: absolute;
    right: -7px;
    top: -3px;
    color: rgb(7, 7, 7);
    font-weight: bold;
    box-shadow: 1 1 5px;
    display: inline-block;
    justify-content: center;
    width: 2vw;
    border-radius: 25px;
    border-width: 0px;
  }
  .restoreTicketButton {
    right: -2.5px;
    top: -1px;
    font-weight: bold;
    box-shadow: 1 1 5px;
    display: inline-block;
    justify-content: center;
    color: blue;
    width: 3vw;
    border-radius: 25px;
    border-width: 1px;
  }
  .results {
    display: flex;
    justify-content: center;
    width: 33vw;
  }
  
  .label {
    margin: 10;
    justify-content: left;
    border-radius: 10px;
    background-color: rgb(179, 224, 255);
  }
  
*{box-sizing:border-box}*::before,*::after{box-sizing:border-box}body{font-family:"Mukta", sans-serif;font-size:1rem;line-height:1.5;display:flex;align-items:center;justify-content:center;margin:0;min-height:100vh;background:#f3f8fa}button{position:relative;display:inline-block;cursor:pointer;outline:none;border:0;vertical-align:middle;text-decoration:none;background:transparent;padding:0;font-size:inherit;font-family:inherit}button.learn-more{width:12rem;height:auto}button.learn-more .circle{transition:all .45s cubic-bezier(0.65, 0, 0.076, 1);position:relative;display:block;margin:0;width:2rem;height:2rem;background:#1ca4ff;border-radius:1.625rem}button.learn-more .circle .icon{transition:all .45s cubic-bezier(0.65, 0, 0.076, 1);position:absolute;top:0;bottom:0;margin:auto;background:#fff}button.learn-more .circle .icon.arrow{transition:all .45s cubic-bezier(0.65, 0, 0.076, 1);left:0.215rem;width:1.125rem;height:0.125rem;background:none}button.learn-more .circle .icon.arrow::before{position:absolute;content:"";top:-0.25rem;right:0.0625rem;width:0.625rem;height:0.625rem;border-top:0.125rem solid #fff;border-right:0.125rem solid #fff;transform:rotate(45deg)}button.learn-more .button-text{transition:all .45s cubic-bezier(0.65, 0, 0.076, 1);position:absolute;top:0;left:0;right:0;bottom:0;padding:0 0.75rem 0;margin:0.185rem 1.75rem 2rem 2.2rem;color:#1ca4ff;font-weight:700;line-height:1.6;text-align:center;text-transform:uppercase}button:hover .circle{width:100%}button:hover .circle .icon.arrow{background:#fff;transform:translate(1rem, 0)}button:hover .button-text{color:#fff}@supports (display: grid){body{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:0.625rem;grid-template-areas:". main main ." ". main main ."}#container{grid-area:main;align-self:center;justify-self:center}}

